@import url("https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@font-face {
  font-family: "AvionicWideObliqueBlack";
  src: url("../fonts/AvionicWideObliqueBlack.otf") format("opentype");
}
body {
  -webkit-font-smoothing: antialiased;
  scroll-behavior: smooth;
}

:root {
  --step-0: clamp(1.13rem, calc(1.04rem + 0.43vw), 1.38rem);
  --step-1: clamp(1.27rem, calc(1.13rem + 0.67vw), 1.65rem);
  --step-2: clamp(1.42rem, calc(1.23rem + 0.97vw), 1.98rem);
  --step-3: clamp(1.6rem, calc(1.33rem + 1.35vw), 2.38rem);
  --step-4: clamp(1.8rem, calc(1.44rem + 5vw), 6rem);
  --step-5: clamp(2.03rem, calc(1.54rem + 7vw), 8rem);
  --button-font-size: clamp(0.6rem, calc(0.7rem + 0.43vw), 1.38rem);
  --avionic: "AvionicWideObliqueBlack", sans-serif;
  --exo: "Exo 2", sans-serif;
  --black: rgba(20, 20, 20, 1);
  --grey: rgba(20, 20, 20, 1);
  --white: rgba(255, 255, 255, 1);
  --white-20: rgba(255, 255, 255, 0.2);
  --white-5: rgba(255, 255, 255, 0.5);
  --card-height: 20rem;
  --card-width: calc(var(--card-height) / 1.5);
  --currentOptionColor: transparent;
}

::-moz-selection {
  color: var(--white);
  background-color: var(--black);
}

::selection {
  color: var(--white);
  background-color: var(--black);
}

.invisible {
  visibility: hidden;
  opacity: 0;
}

.visible {
  visibility: visible;
  opacity: 0;
}

.display-hide {
  opacity: 0;
  display: none;
}

.display-show {
  display: block;
  opacity: 1;
}

.blocker {
  position: absolute;
  z-index: 9999;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
  background-color: black;
  color: white;
}

.black-to-white {
  filter: invert(1);
  fill-opacity: 0;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: var(--exo);
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

div,
span,
article,
section,
header,
footer,
aside,
p,
ul,
li,
fieldset,
legend,
label,
a,
nav,
form {
  box-sizing: border-box;
}

ol,
ul,
li {
  list-style: none;
}

a {
  text-decoration: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.t-bold {
  font-weight: 800;
}

.t-normal {
  font-weight: 400;
}

.t-thin {
  font-weight: 200;
}

.t-italic {
  font-style: italic;
}

.t-stroke-black {
  color: transparent;
  -webkit-text-stroke: 1px var(--black);
}

.t-stroke-white {
  color: transparent;
  -webkit-text-stroke: 1px var(--white);
}

.t-left {
  text-align: left;
}

.t-right {
  text-align: right;
}

.t-center {
  text-align: center;
}

.t-white {
  color: var(--white);
}

.t-black {
  color: var(--black);
}

h1 {
  font-size: var(--step-5);
}

h2 {
  font-size: var(--step-4);
}

h3 {
  font-size: var(--step-3);
}

h4 {
  font-size: var(--step-2);
}

h5 {
  font-size: var(--step-1);
}

p,
a {
  font-size: var(--step-0);
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

button,
i {
  cursor: pointer;
}

.specifications-wrapper {
  background-color: var(--grey);
  color: var(--grey);
  display: flex;
  flex-wrap: wrap;
  padding: 2rem;
  gap: 2rem;
}

.table {
  max-width: 100%;
  text-align: center;
  margin: 0 auto;
  border: 1px solid white;
  box-shadow: 0 1px 3px rgba(255, 255, 255, 0.2);
  display: table;
}

.row {
  display: table-row;
  font-weight: 500;
  color: white;
  transition: all 0.5s;
}
.row:hover {
  background-color: white;
  color: black;
  transition: all 0.5s;
}
.row.header {
  font-weight: 700;
  color: var(--grey);
  font-size: var(--step-0);
  background: white;
}

.cell {
  padding: 0.75rem 1rem;
  display: table-cell;
}

body::-webkit-scrollbar {
  background-color: #020202;
  display: hidden;
  width: 0px;
}
body::-webkit-scrollbar-track {
  display: hidden;
  background-color: #020202;
}
body::-webkit-scrollbar-thumb {
  display: hidden;
  background-color: var(--white);
  border-radius: 1px;
}

header {
  width: 100%;
  background-color: rgba(20, 20, 20, 0.9);
  backdrop-filter: blur(20px) saturate(160%) contrast(45%) brightness(140%);
  -webkit-backdrop-filter: blur(20px) saturate(160%) contrast(45%) brightness(140%);
  position: sticky;
  inset: 0 0 auto 0;
  z-index: 9999;
  padding: 1rem 5rem;
}
@supports ((-webkit-backdrop-filter: blur(30px) saturate(160%) contrast(45%) brightness(140%)) or (backdrop-filter: blur(30px) saturate(160%) contrast(45%) brightness(140%))) {
  header header {
    background-color: rgba(20, 20, 20, 0.9);
    backdrop-filter: blur(20px) saturate(160%) contrast(45%) brightness(140%);
    -webkit-backdrop-filter: blur(20px) saturate(160%) contrast(45%) brightness(140%);
  }
}
header .navbar {
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 5%;
  color: white;
}
header .navbar #navbar-Logo {
  cursor: pointer;
  max-width: 15%;
  min-width: 6rem;
  filter: invert(1);
}
header .fa-bars {
  font-size: var(--step-0);
}
header .navbar-CTA {
  font-size: var(--button-font-size);
  white-space: nowrap;
  padding: 0.5rem 1rem;
  border: 1px solid var(--white);
  color: var(--white);
  background-color: transparent;
  transition: background-color 0.5s, color 0.5s;
  margin-left: auto;
}
header .navbar-CTA:is(:hover, :active) {
  color: var(--black);
  background-color: var(--white);
  transition: background-color 0.5s, color 0.5s;
}
header .menu {
  position: fixed;
  overflow: hidden;
  inset: 0;
  width: 100%;
  height: 100svh;
  background-color: rgba(20, 20, 20, 0.99);
  backdrop-filter: blur(20px) saturate(160%) contrast(45%) brightness(140%);
  -webkit-backdrop-filter: blur(20px) saturate(160%) contrast(45%) brightness(140%);
  transform: translateX(-100%);
}
header .menu .button {
  position: absolute;
  right: 5%;
  right: 5%;
  top: 1rem;
  font-size: var(--step-3);
}
header .menu .button .fa-times {
  color: red;
}
header .menu ul {
  display: flex;
  justify-content: center;
  line-height: 4rem;
  text-align: center;
  height: 100dvh;
  flex-direction: column;
}
header .menu ul li a {
  font-size: var(--step-3);
  text-decoration: none;
  color: white;
  transition: all 0.5s;
}

.index .hero {
  width: 100%;
  height: 100vh;
  background-color: var(--grey);
}
.index .hero .swiper {
  width: 100vw;
  height: 100vh;
  background-color: var(--grey);
}
.index .hero .swiper .swiper-slide {
  position: relative;
}
.index .hero .swiper .swiper-slide .hero-bg-img {
  width: 100%;
  min-width: 100%;
  height: 100vh;
  object-fit: cover;
  -o-object-fit: cover;
  object-position: right center;
  -o-object-position: right center;
}
.index .hero .swiper .swiper-slide .card {
  position: absolute;
  background-color: var(--grey);
  color: var(--white);
  z-index: 9999;
  padding: 1rem;
  top: 60%;
  left: 0;
  width: clamp(20rem, 40%, 50rem);
}
.index .hero .swiper .swiper-button-prev,
.index .hero .swiper .swiper-button-next {
  color: var(--white);
}

.single-bike .hero {
  position: relative;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}
.single-bike .hero .parallax {
  transform: translate(-50%, -50%);
  transition: 0.45s cubic-bezier(0.2, 0.49, 0.32, 0.99);
}
.single-bike .hero .bg-img {
  position: absolute;
  z-index: 0;
  width: 2800px;
  top: 50%;
  left: 50%;
}
.single-bike .hero .text {
  position: absolute;
  z-index: 2;
  top: calc(50% - 250px);
  left: 50%;
  text-align: center;
  text-transform: uppercase;
  color: var(--white);
  pointer-events: auto;
}
.single-bike .hero .text h1 {
  font-weight: 800;
  font-family: var(--avionic);
}
.single-bike .hero .text h2 {
  margin-top: -0.5rem;
  font-weight: 300;
  letter-spacing: 0.3rem;
  font-size: var(--step-3);
}
.single-bike .hero .bike {
  position: absolute;
  bottom: 0;
  z-index: 3;
  height: 100%;
  top: 51%;
  left: 50%;
}
.single-bike .hero .fog-2 {
  position: absolute;
  z-index: 4;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 50%;
}
.single-bike .hero .vignette {
  position: absolute;
  z-index: 100;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.7));
}
.single-bike .view-360 {
  color: white;
  width: 100vw;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.single-bike .view-360 .wrapper-360 {
  width: clamp(25rem, 70%, 60vw);
  background-color: white;
  overflow: hidden;
}
.single-bike .currentOption {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.2rem;
  flex-direction: column;
  margin: 1rem;
  top: -50%;
  padding: 0.5rem 2rem;
  border: 3px solid black;
  border-radius: 20px;
  background-color: var(--currentOptionColor);
}
.single-bike .currentOption p {
  font-size: 16px;
}
.single-bike .options-wrapper {
  gap: 1rem;
  flex-wrap: wrap;
  padding: 0 2rem;
}
.single-bike .options-wrapper button {
  border: 0;
  height: 4rem;
  width: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: red;
  margin: 2rem 0;
  border-radius: 100%;
}
.single-bike .options-wrapper button:is(:active, :hover) {
  outline: 2px solid black;
  outline-offset: 2px;
}
.single-bike .options-wrapper .activeBtn {
  outline: 2px dashed black;
  outline-offset: 2px;
  animation: rotate infinite 5s linear;
}
@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}
.single-bike .options-wrapper .blueOption {
  background-color: #54ccfe;
  --currentOptionColor: #54ccfe;
}
.single-bike .options-wrapper .neonOption,
.single-bike .options-wrapper .neonAlloyOption {
  background-color: #ccff00;
  --currentOptionColor: #ccff00;
}
.single-bike .options-wrapper .orangeOption,
.single-bike .options-wrapper .orangeAlloyOption {
  background-color: #fa4519;
  --currentOptionColor: #fa4519;
}
.single-bike .options-wrapper .redOption,
.single-bike .options-wrapper .redAlloyOption {
  background-color: #f82844;
  --currentOptionColor: #f82844;
}
.single-bike .options-wrapper .spok-wheels,
.single-bike .options-wrapper .alloy-wheels {
  width: 100%;
}
.single-bike .sectionStyle {
  width: 100%;
  background-color: var(--white);
  color: black;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0rem 2rem;
}
.single-bike .sectionStyle img {
  max-width: 100%;
  position: relative;
  height: auto;
  display: block;
  background: radial-gradient(circle at center, rgba(153, 223, 250, 0.2) 30%, rgba(255, 255, 255, 0) 50%);
}
.single-bike .bgToBlack {
  color: white;
}
.single-bike .clutch {
  background-color: var(--grey);
  color: white;
}
.single-bike .clutch .clutchImg-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.single-bike .illuminate {
  width: 100vw;
  height: 100vh;
  background-color: var(--grey);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}
.single-bike .illuminate p {
  font-size: var(--step-5);
  font-weight: 900;
  text-align: center;
  cursor: default;
}
.single-bike .illuminate .glow-gradient {
  color: #202020;
  text-shadow: 0 0 0px #ffffff;
  text-shadow: none;
}
.single-bike .illuminate .t-glow {
  color: var(--white);
  text-shadow: 0 0 10px #ffffff;
}
.single-bike .led {
  background-color: var(--grey);
}
.single-bike .led img {
  max-width: 100%;
}

.animPage {
  height: 100vh;
  width: 100vw;
  background-color: var(--grey);
}

.keyTextAnimations {
  position: relative;
  width: 100%;
  height: 100%;
}
.keyTextAnimations .key1 {
  position: absolute;
  top: 50%;
  left: 5%;
  width: 40%;
  font-weight: 300;
}

.keyTextAnimations2 {
  position: relative;
  width: 100%;
  height: 100%;
}
.keyTextAnimations2 .key2 {
  position: absolute;
  top: 20%;
  left: 3%;
  width: 40%;
  font-weight: 500;
}
.keyTextAnimations2 .key3 {
  position: absolute;
  top: 20%;
  right: 3%;
  width: 40%;
  font-size: 16px;
  font-weight: 300;
}
.keyTextAnimations2 .key4 {
  position: absolute;
  top: 50%;
  right: 3%;
  width: 40%;
  font-size: 16px;
  font-weight: 300;
}

.footer-section {
  width: 100%;
  background: #141414;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 2rem;
}
.footer-section a {
  color: #fff;
}
.footer-section .links {
  width: 100%;
  justify-content: space-between;
  margin: 0 auto;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  padding: 1rem 2rem;
}
.footer-section .footer-top {
  margin-bottom: -1rem;
  transition: background-color 0.5s, color 0.5s;
}
.footer-section .footer-top:hover,
.footer-section .footer-bottom:hover {
  background-color: white;
  transition: background-color 0.5s, color 0.5s;
}
.footer-section .footer-top:hover a,
.footer-section .footer-bottom:hover a {
  color: black;
  font-weight: 500;
  transition: all 0.5s;
}
.footer-section .footer-top:hover :not(a:hover),
.footer-section .footer-bottom:hover :not(a:hover) {
  color: #808080;
  font-weight: 400;
  transition: all 0.5s;
}
.footer-section .footer-bottom {
  margin-top: -1rem;
  transition: background-color 0.5s, color 0.5s;
}
.footer-section .zontes-logo {
  filter: invert(1);
  width: 11rem;
  margin: 0 auto;
  transition: filter 0.25s;
}
.footer-section .zontes-logo:hover {
  filter: invert(0.5);
  transition: filter 0.5s;
  margin: 0 auto;
}

.about-us-wrapper {
  background-color: black;
  position: relative;
  padding: 0 5rem 5rem;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.about-us-wrapper .zontes-vid-1 {
  position: absolute;
  z-index: 0;
  inset: 0;
  top: -14%;
  width: 100%;
  mask-image: linear-gradient(black, transparent);
  -webkit-mask-image: linear-gradient(black, transparent);
}
.about-us-wrapper .about-us-details {
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about-us-wrapper .about-us-logo {
  width: clamp(5rem, 20%, 30rem);
  margin: 0 auto;
  transition: filter 0.25s;
  pointer-events: none;
}
.about-us-wrapper .about-us-logo:hover {
  filter: invert(0.5);
  transition: filter 0.5s;
  margin: 0 auto;
}
.about-us-wrapper .about-us-desc {
  line-height: 2rem;
  margin-bottom: 2rem;
}
.about-us-wrapper #zontes-logo {
  filter: invert(1);
  margin-bottom: 2rem;
}

.Iron-Monk-Logo {
  filter: invert(1);
  width: clamp(5rem, 20%, 30rem);
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 0;
}

.home-models {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  background-color: var(--grey);
  color: white;
  padding: 4rem;
}
.home-models .models {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  gap: 2rem;
}
.home-models .card {
  width: var(--card-width);
  height: var(--card-height);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding: 0 3rem;
  perspective: 2500px;
}
.home-models .cover-image {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.home-models .wrapper {
  transition: all 0.5s;
  position: absolute;
  width: 100%;
  z-index: -1;
}
.home-models .cover-image {
  transition: filter 0.5s;
}
.home-models .card:hover .wrapper {
  transform: perspective(900px) translateY(-5%) rotateX(25deg) translateZ(0);
  box-shadow: 0 1px 3px rgba(255, 255, 255, 0.2);
  -webkit-box-shadow: 0 1px 3px rgba(255, 255, 255, 0.2);
  -moz-box-shadow: 0 1px 3px rgba(255, 255, 255, 0.2);
}
.home-models .card:hover .wrapper .cover-image {
  filter: blur(2px);
  transition: filter 0.5s;
}
.home-models .title {
  width: 100%;
  transition: transform 0.5s;
}
.home-models .card:hover .title {
  transform: translate3d(0%, -50px, 100px);
}
.home-models .bike-model {
  width: 100%;
  opacity: 0;
  transition: all 0.5s;
  position: absolute;
  z-index: -1;
}
.home-models .card:hover .bike-model {
  opacity: 1;
  transform: translate3d(0%, -30%, 100px);
}

.index-loader-video-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.index-loader-video-wrapper .index-logo {
  box-sizing: border-box;
  width: clamp(5rem, 50%, 30rem);
}

.animpage {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow-x: hidden;
  background-color: #000;
}

.features-wrapper {
