  }
}
.seating {
  .topHalf {
    background-image: url(../images/features/seat.webp);
    background-size: cover;
    width: 100%;
    height: 50vh;
  }
}

.contact {
  background-color: var(--grey);
  padding: 1rem;

  .contactForm-wrapper {
    display: flex;
    flex-wrap: wrap;
  }

  .section-header {
    text-align: center;
    color: #fff;
    margin: 1rem 0;
  }

  form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    width: 50%;
    margin: 0 auto;
    padding: 1rem;
  }

  .form-group {
    width: 100%;

    input, textarea, select,
    button {
      width: 100%;
    }
    textarea{
      margin-top: 2rem;
    }

    #Message {
      height: 5rem;
    }
  }

  .form-control, select {
    background-color: var(--grey);
    border: 1px solid white;
    padding: 1rem;
    color: white;

  }

  #submit {
    font-size: var(--button-font-size);
    white-space: nowrap;
    padding: 1rem 2rem;
    border: 1px solid var(--white);
    color: var(--white);
    background-color: transparent;
    transition: background-color 0.5s, color 0.5s;
    margin-left: auto;

    &:is(:hover, :active) {
      color: var(--black);
      background-color: var(--white);
      transition: background-color 0.5s, color 0.5s;
    }
  }
}
